"use client";

import React, { useState } from 'react';
import { Mail, Copy, History, Loader2, Check } from 'lucide-react';

export default function EmailAssistantPage() {
  const [loading, setLoading] = useState(false);
  const [recipient, setRecipient] = useState('');
  const [subject, setSubject] = useState('');
  const [keyPoints, setKeyPoints] = useState('');
  const [tone, setTone] = useState('Professional');
  const [output, setOutput] = useState('');
  const [copied, setCopied] = useState(false);

  const handleGenerate = () => {
    setLoading(true);
    setTimeout(() => {
      setOutput(`Subject: ${subject}\n\nHi ${recipient},\n\nI hope you're having a productive week.\n\n${keyPoints}\n\nLet me know your thoughts.\n\nBest,\n[Your Name]`);
      setLoading(false);
    }, 1500);
  };

  const handleCopy = () => {
    navigator.clipboard.writeText(output);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <div className="min-h-screen bg-[#0B1120] text-white p-6 lg:p-8">
      <div className="max-w-[1600px] mx-auto grid grid-cols-1 lg:grid-cols-2 gap-8">
        
        {/* Input Card */}
        <div className="bg-[#1E293B] rounded-2xl border border-gray-800 p-6 shadow-xl h-fit">
          <div className="flex items-center gap-3 mb-6">
            <Mail className="text-[#00C26D] h-6 w-6" />
            <h2 className="text-xl font-bold">Email Details</h2>
          </div>

          <div className="space-y-5">
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <label className="text-sm text-gray-400 font-medium">Recipient Name</label>
                <input 
                  type="text" 
                  value={recipient}
                  onChange={(e) => setRecipient(e.target.value)}
                  className="w-full bg-[#0F172A] border border-gray-700 rounded-lg px-4 py-3 text-white focus:ring-1 focus:ring-[#00C26D] focus:border-[#00C26D] outline-none transition-all"
                  placeholder="e.g. John Doe"
                />
              </div>
              <div className="space-y-2">
                <label className="text-sm text-gray-400 font-medium">Tone</label>
                <select 
                  value={tone}
                  onChange={(e) => setTone(e.target.value)}
                  className="w-full bg-[#0F172A] border border-gray-700 rounded-lg px-4 py-3 text-white focus:ring-1 focus:ring-[#00C26D] focus:border-[#00C26D] outline-none transition-all"
                >
                  <option>Professional</option>
                  <option>Friendly</option>
                  <option>Urgent</option>
                  <option>Cold Outreach</option>
                </select>
              </div>
            </div>

            <div className="space-y-2">
              <label className="text-sm text-gray-400 font-medium">Subject Line</label>
              <input 
                type="text" 
                value={subject}
                onChange={(e) => setSubject(e.target.value)}
                className="w-full bg-[#0F172A] border border-gray-700 rounded-lg px-4 py-3 text-white focus:ring-1 focus:ring-[#00C26D] focus:border-[#00C26D] outline-none transition-all"
                placeholder="e.g. Partnership Opportunity"
              />
            </div>

            <div className="space-y-2">
              <label className="text-sm text-gray-400 font-medium">Key Points / Context</label>
              <textarea 
                value={keyPoints}
                onChange={(e) => setKeyPoints(e.target.value)}
                className="w-full bg-[#0F172A] border border-gray-700 rounded-lg px-4 py-3 text-white focus:ring-1 focus:ring-[#00C26D] focus:border-[#00C26D] outline-none min-h-[150px] resize-none transition-all"
                placeholder="What do you want to say?"
              />
            </div>

            <button 
              onClick={handleGenerate}
              disabled={loading}
              className="w-full bg-[#00C26D] hover:bg-[#00A05A] text-white font-bold py-3 rounded-lg transition-all shadow-lg shadow-green-900/20 flex items-center justify-center gap-2 mt-4"
            >
              {loading ? <Loader2 className="animate-spin h-5 w-5" /> : "Generate Email Draft"}
            </button>
          </div>
        </div>

        {/* Output Card */}
        <div className="bg-[#1E293B] rounded-2xl border border-gray-800 p-6 shadow-xl h-full min-h-[500px] flex flex-col">
          <div className="flex justify-between items-center mb-6 border-b border-gray-800 pb-4">
            <h2 className="text-xl font-bold">Generated Email</h2>
            <div className="flex items-center gap-3">
              <button className="text-sm text-gray-400 hover:text-white transition-colors flex items-center gap-2">
                <History className="h-4 w-4" /> History
              </button>
              <button 
                onClick={handleCopy}
                className="text-sm text-[#00C26D] hover:text-[#00A05A] transition-colors flex items-center gap-2"
              >
                {copied ? <Check className="h-4 w-4" /> : <Copy className="h-4 w-4" />}
                {copied ? "Copied" : "Copy"}
              </button>
            </div>
          </div>
          <div className="flex-1 bg-[#0F172A] rounded-xl p-6 border border-gray-800">
            {output ? (
              <p className="whitespace-pre-wrap text-gray-300 leading-relaxed font-light">{output}</p>
            ) : (
              <div className="h-full flex flex-col items-center justify-center text-gray-600">
                <Mail className="h-12 w-12 mb-4 opacity-20" />
                <p>Enter details to draft your email.</p>
              </div>
            )}
          </div>
        </div>

      </div>
    </div>
  );
}